<template lang="">
    <div class="d-flex align-center" style="gap: 10px; width: 600px;">
        <flag :iso="team1.icon" />
        <span style="width: 90px;">
            {{ team1.name }}
        </span>
        <v-text-field
            label="Gole"
            variant="outlined"
            hide-details
            style="width: 1px;"
            type="number"
            @update:modelValue="(e) => addGoalsScored(e, team1.name)"
        ></v-text-field>
        <span class="text-h3">
            {{ ':' }}
        </span>
        <v-text-field 
            label="Gole" 
            variant="outlined"
            hide-details
            style="width: 1px;"
            type="number"
            @update:modelValue="(e) => addGoalsScored(e, team2.name)"
        ></v-text-field>
        <span style="width: 90px; display: flex; justify-content: end;">
            {{ team2.name }}
        </span>
        <flag :iso="team2.icon" />
    </div>
</template>
<script>
export default {
    name: 'Match',
    props: {
        team1: {
            type: Object,
            required: true
        },
        team2: {
            type: Object,
            required: true
        },
        addGoalsScored: {
            type: Function,
            required: true
        }
    }
}
</script>
