<template lang="">
    <div class="d-flex flex-column align-center" style="gap: 10px; width: 800px; height: 600px">
        <div>
            <h1 class="text-h4">GRUPA {{ groupName }}</h1>
        </div>
        <div class="d-flex align-center" style="gap: 10px; width: 800px; height: 100px">
            <v-select
                v-for="team in [1, 2, 3, 4]"
                :key="team"
                :label="`Drużyna ${team}`"
                :items="teams"
                variant="outlined"
                return-object
                @update:modelValue="(e) => changeTeam(e, team)"
                hide-details
                width="200"
            >
                <template v-slot:selection="{ item }">
                    <div class="d-flex align-center" style="gap: 10px;">
                        <flag :iso="item.props.value.icon" />
                        {{ item.props.value.name }}
                    </div>
                </template>
                <template v-slot:item="{ item, props }">
                    <v-list-item v-bind="props" title="" :key="item.props.value.name">
                        <flag :iso="item.props.value.icon" />
                        {{ item.props.value.name }}
                    </v-list-item>
                </template>
            </v-select>
        </div>
        <div v-if="team1 && team2 && team3 && team4" class="d-flex flex-column align-center" style="gap: 10px; width: 600px;">
            <Match :team1="team1" :team2="team2" :addGoalsScored="addGoalsScored"/>
            <Match :team1="team1" :team2="team3" :addGoalsScored="addGoalsScored"/>
            <Match :team1="team1" :team2="team4" :addGoalsScored="addGoalsScored"/>
            <Match :team1="team2" :team2="team3" :addGoalsScored="addGoalsScored"/>
            <Match :team1="team2" :team2="team4" :addGoalsScored="addGoalsScored"/>
            <Match :team1="team3" :team2="team4" :addGoalsScored="addGoalsScored"/>
        </div>
    </div>
</template>
<script>
import Match from '@/components/Match.vue';

export default {
    name: 'Group',
    components: {
        Match,
    },
    data() {
        return {
            team1: null,
            team2: null,
            team3: null,
            team4: null
        }
    },
    props: {
        teams: {
            type: Array,
            required: true
        },
        groupName: {
            type: String
        },
        addGoalsScored: {
            type: Function,
            required: true
        }
    },
    methods: {
        changeTeam(e, team) {
            console.log(e, team)
            if (team === 1) {
                this.team1 = e
            } else if (team === 2) {
                this.team2 = e
            } else if (team === 3) {
                this.team3 = e
            } else if (team === 4) {
                this.team4 = e
            }
        }
    },
}
</script>
